import React from "react"
import { Link, graphql, useStaticQuery } from "gatsby"

import Layout from "../components/Layout"
import SEO from "../components/seo"
import Alert from "react-bootstrap/Alert"
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"
import Badge from "react-bootstrap/Badge"
import Img from "gatsby-image";
import Price from "../components/Price";

const Tags = {
  PORK: "Pork",
  BEEF: "Beef",
  CHICKEN: "Chicken",
  FISH: "Fish",

  SPICY: "Spicy",
  SWEET: "Sweet",
};

const Category = {
  MAIN: "Main",
  SIDE: "Side",
};

const { PORK, BEEF, CHICKEN, FISH, SPICY, SWEET } = Tags;

const dishes = [
  { image: "box-hong-shao-rou.jpg", title: "Red Roast Pork Belly", tags: [PORK], price: 9.75, category: Category.MAIN },
  { image: "dish-pai-gu.jpg", title: "Pork Ribs", tags: [PORK], price: 9.75, category: Category.MAIN },
  { image: "dish-meat-ball.jpg", title: "Lion's Head Meat Balls", tags: [PORK], price: 9.75, category: Category.MAIN },
  { image: "dish-gan-bian-la-zi-ji.jpg", title: "Spicy Crispy Chicken", tags: [SPICY, CHICKEN], price: 9.75, category: Category.MAIN },
  { image: "dish-la-jiao-ji-ding.jpg", title: "Spicy Chicken Stir-Fry", tags: [SPICY, CHICKEN], price: 9.75, category: Category.MAIN },
  { image: "dish-korean-chicken-finger.jpg", title: "Sweet Chicken Fingers", tags: [SWEET, CHICKEN], price: 9.75, category: Category.MAIN },
  { image: "", title: "Red Roast Beef with Potato", tags: [BEEF], price: 9.75, category: Category.MAIN },
  { image: "", title: "Spicy Beef with Potato", tags: [SPICY, BEEF], price: 9.75, category: Category.MAIN },
  { image: "dish-sweet-sour-fish.jpg", title: "Sweet and Sour Fish", tags: [SWEET, FISH], price: 9.75, category: Category.MAIN },
  { image: "dish-shanghai-nasi.jpg", title: "Shanghai Nasi", tags: [CHICKEN], price: 5.50, category: Category.MAIN },
  { image: "dish-char-siu-nasi.jpg", title: "Char Siu Nasi", tags: [PORK], price: 5.95, category: Category.MAIN },
  { image: "dish-jian-bing-guo-zi.jpg", title: "Chinese Savory Pancake", tags: [PORK], price: 4.25, category: Category.MAIN },
  { image: "", title: "Popcorn Chicken", tags: [CHICKEN], price: 4.25, category: Category.SIDE },
  { image: "snack-char-siu-babao.jpg", title: "Char Siu Babao", tags: [PORK], price: 3.75, category: Category.SIDE },
  { image: "snack-siu-mai.jpg", title: "Siu Mai", tags: [PORK], price: 2.75, category: Category.SIDE },
];

export const menuPageDishImage = graphql`
  fragment menuPageDishImage on File {
    childImageSharp {
      fluid {
        ...GatsbyImageSharpFluid
      }
    }
  }
`;

// export const query = graphql`
//   query {

//   }
// `;

const MenuPage = () => (
  <Layout>
    <SEO title="Menu" />
    <h1 className="display-1">Menu</h1>
    <p className="lead">Our standard menu available for delivery or pick-up at the store around dinner time.</p>

    <Row>
      <Col md={{ span: 8, offset: 2 }}>
        <Alert variant="info">
          Apart from our standard menu, we also provide complete <Link to="/bento">lunch and dinner meal-boxes</Link>.
          We constantly vary these meals and announce them one day ahead through WeChat and WhatsApp.
        </Alert>
      </Col>
    </Row>

    <h2 className="mt-5">Main Dishes</h2>
    <Row>
      {dishes.filter(({ category }) => category === Category.MAIN).map(dish => (
        <Col key={dish.title} className="mt-3 mb-3" md={4} sm={6}>
          <Dish {...dish} />
        </Col>
      ))}
    </Row>

    <h2 className="mt-5">Side Dishes</h2>
    <Row>
      {dishes.filter(({ category }) => category === Category.SIDE).map(dish => (
        <Col key={dish.title} className="mt-3 mb-3" md={4} sm={6}>
          <Dish {...dish} />
        </Col>
      ))}
    </Row>
  </Layout >
)

function Dish({ image, title, tags, price, ...props }) {
  const data = useStaticQuery(graphql`
    query {
      images: allFile(filter: {
        sourceInstanceName: { eq: "images" }
      }) {
        edges {
          node {
            relativePath,
            childImageSharp {
              fluid(quality: 90) {
                ...GatsbyImageSharpFluid,
              }
            }
          }
        }
      }
      placeholder: file(relativePath: { eq: "store-front.jpg" }) {
        childImageSharp {
          fluid(quality: 90) {
            ...GatsbyImageSharpFluid,
          }
        }
      }
    }`);

  const edge = data.images.edges.find(({ node }) => node.relativePath === image);
  const fluid = edge === undefined ? data.placeholder.childImageSharp.fluid : edge.node.childImageSharp.fluid;
  return (
    <>
      <Img sizes={{ ...fluid, aspectRatio: 4/3 }} alt={title} />
      <h5>{title}</h5>
      <h6><Price amount={price}/></h6>
      <div>
        {
          tags.map((tag, index) => ([
            index > 0 && " ",
            <Badge variant="info">{tag}</Badge>
          ]))
        }
      </div>
    </>
  );
}

export default MenuPage
